.footer{
    
    background-color: #F9F9F9;
    padding-top:2%;
    padding-bottom: 1%;
}

.site-footer:before { /* this is the border */
    content:"";
    display:block;
    width: 100%;
    max-width:100%;
    border-bottom: 2px solid #F3C577;
    margin: 0 auto 2%;
}

.footerImg{
    max-width: 100%; height: auto;
    margin-bottom: 5%;
}

.noDots{
list-style: none;
}

.footerLink{
    color: #8a8a8a;
    font-size: .9rem;
}

.widget-title{
    color: #D11D26;
    font-weight: 700;
    margin-bottom: .5rem;
}

.menu{
    padding: 0;
}

.footerImg{
    width: 100%;
    height: auto;
}
.imgCol{
    margin-right: 5%;
}

.site-footer{
    margin-bottom: 3%;
}

@media (max-width: 768px) { 
.mobileNone{
display: none;
}
}