.fixed-btn{
    position: fixed;
    top: 85%;
    left:88%;
    right:1%;
    bottom: 5%;
    color: #fff !important;

text-transform: uppercase;
text-decoration: none;
background: #ed3330;
padding: 15px;
border-radius: 5px;
display: inline-block;
border: none;
transition: all 0.4s ease 0s;
}

.fixed-btn:hover {
    background: #434343;
    letter-spacing: 1px;
    -webkit-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
    -moz-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
    box-shadow: 5px 40px -10px rgba(0,0,0,0.57);
    transition: all 0.4s ease 0s;
    }
.dot {
    height: 13px;
    width: 13px;
    background-color: rgb(13, 181, 4);
    border-radius: 50%;
    display: inline-block;
  }